* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.block {
  display: flex;
  height: 300px;
  flex-basis: 100%;
  align-items: center;
  justify-content: center;
  font-size: 100px;
  color: #fff;
}

.block--1 {
  background-color: rgb(100%, 0%, 0%);
}

.block--2 {
  background-color: rgb(80%, 0%, 0%);
}

.block--3 {
  background-color: rgb(60%, 0%, 0%);
}

.block--4 {
  background-color: rgb(40%, 0%, 0%);
}

.block--5 {
  background-color: rgb(20%, 0%, 0%);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) and (max-width: 899px) {
  .container {
    display: flex;
    flex-wrap: wrap;
  }
  
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 1;
  }

  .block--5,
  .block--6 {
    order: 2;
  }
}

@media (min-width: 900px) {
  .container {
    display: flex;
    flex-wrap: wrap;
  }

  .block {
    flex-basis: 33.33%;
  }

  .block--4 {
    order: 2;
  }

  .block--5 {
    order: 1;
  }
}
